html { height: 100%;}

::-moz-selection {
  background: #fff333;
}

::selection {
  background: #fff333;
}

body {
  background-color: rgb(0, 0, 0);
  font-family: monospace;
  font-size: 16px;
  line-height: 1.6;
  margin: 0;
  min-height: 100%;
  overflow-wrap: break-word;
  filter: invert(100%);
}

.post-meta { text-align: right; }

h1 {font-size:1.8rem;}
h2 {font-size: 1.5rem;}

h2, h3, h4, h5, h6 { margin-top: 3rem; }

hr { margin: 2rem 0; }

p { margin: 1rem 0; }

li { margin: 0.4rem 0; }

*:target { background: yellow; }

.w {
  max-width: 720px;
  margin: 0 auto;
  padding: 4rem 2rem;
}

.toc {
  border: thin solid black;
  padding: 1rem;
}

hr {
  text-align: center;
  border: 0;

  &:before { content: '/////' }
  &:after { content: attr(data-content) '/////' }
}

pre {
  padding: 1em;
  overflow-x: auto; /* Fix pre content overflowing parent without scroll bar */
  filter: invert(100%);
}

table { width: 100%; }

table, th, td {
  border: thin solid black;
  border-collapse: collapse;
  padding: 0.4rem;
}

code:not(pre > code) {
  padding: 0.1em 0.2em;
  font-size: 85%;
  font-style: italic;
  filter: invert(20%);
}

code {
  font-size: 80%;
}

code.has-jax {
  -webkit-font-smoothing: antialiased;
  background: inherit !important;
  border: none !important;
  font-size: 100%;
}

blockquote {
  font-style: italic;
  border: thin solid black;
  padding: 1rem;

  p { margin: 0; }
}

img {
  max-width: 100%;
  display: block;
  margin: 0 auto;
  filter: invert(100%);
}

figcaption {
  text-align: center;
  opacity: 0.5;
}

.invert {
  filter: invert(100%);
}

.accent {
  color:#fff333
}